<!--  -->
<template>
	<div class="list" ref="wrapper">
		<div>
			<div class="area">
				<div class="title border-topbottom"></div>
				<ul class="button-list">
					<li class="button-wrapper">
						<div class="button">{{this.city}}</div>
					</li>
				</ul>
			</div>
			<div class="area">
				<div class="title border-topbottom"></div>
				<ul class="button-list">
					<li class="button-wrapper" v-for="item in hot" :key="item.id" @click="handleCity(item.name)">
						<div class="button">{{item.name}}</div>
					</li>
				</ul>
			</div>
			<div class="area" v-for="(item,key) in cities" :key="key" :ref="key">
				<div class="title border-topbottom">{{key}}</div>
				<ul class="item-list">
					<li class="item border-bottom" v-for="i in item" :key="i.id" @click="handleCity(i.name)">{{i.name}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
import { setCity } from "js/cache"; // 
import Bscroll from "better-scroll";
export default {
    name: "CityList",
    props: ["cities", "hot", "letter"],
    methods: {
        ...mapMutations({
            changeCity: "SET_CITY" // changeCitymutationsSET_CITY
        }),
        handleCity(city) {
            // changeCitymutation
            this.changeCity(city); // this.$store.commit("changeCity", city);
            setCity(city); // 

            //
            this.$router.push("/");

            //dispatchchangeCitycityactions
            //this.$store.dispatch("changeCity", city);

            //commitmutationsactions
            //this.$store.commit("changeCity", city);
        }
    },
    computed: {
        // vuex(getters.js)
        // this.$store.getters.city(vuex)
        ...mapGetters(["city"]) // this.city
    },
    mounted() {
        this.scroll = new Bscroll(this.$refs.wrapper);
    },
    watch: {
        //letter
        letter() {
            console.log(this.letter);
            if (this.letter) {
                const element = this.$refs[this.letter][0];
                this.scroll.scrollToElement(element);
            }
        }
    }
};
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.border-topbottom
	&:before
		border-color #ccc
	&:after
		border-color #ccc
.border-bottom
	&:before
		border-color #ccc
.list
	overflow hidden
	position absolute
	top 1.58rem
	left 0
	right 0
	bottom 0
	.button-list
		padding 0.1rem 0.6rem 0.1rem 0.1rem
		clearfix()
		.button-wrapper
			width 33.33%
			float left
			.button
				text-align center
				margin 0.1rem
				padding 0.15rem 0
				border 0.02rem solid #ccc
				border-radius 0.06rem
	.title
		line-height 0.76rem
		background #eee
		padding-left 0.2rem
		color #666
		font-size 0.26rem
	.item-list
		.item
			line-height 0.76rem
			padding-left 0.2rem
</style>



